import React from 'react';
import NavBar from "./NavBar";
import {Footer} from "flowbite-react";

const FooterBar = () => {
    return (
        <Footer container={true}>
            <div className="w-full text-center">
                <Footer.Divider />
                <Footer.Copyright
                    href="#"
                    by="Gabriel Beltran"
                    year={2022}
                />
            </div>
        </Footer>
    );
};

export default FooterBar;